/* ENCABEZADO */
.encabezado {
  width: 100%;
  background-color: #b71540;
  display: flex;
  justify-content: space-between;
  height: 90px;
  padding: 0 90px;
  align-items: center;
}
.encabezado .logo {
  display: flex;
  align-items: center;
}
.encabezado .logo h2 {
  color: #FFFFFF;
  font-weight: bold;
  margin-left: 16px;
  cursor: pointer;
}
.encabezado .navigator ul {
  list-style: none;
  display: flex;
  align-items: center;
  /*
  button {
      height: 90px;
      @include mx.boton(4px, 11px);
      display: flex;
      align-items: center;
      &:hover {
          @include mx.boton-hover(4px, 11px, #0098C0, 5px);
      }
      p {
          font-size: 14px;
      }
      img {
          width: 20px;
          margin-left: 8px;
      }
  }
  */
}
.encabezado .navigator ul li {
  padding-right: 30px;
}
.encabezado .navigator ul li a {
  text-decoration: none;
  color: #FFFFFF;
  transition: all 500ms ease-in-out;
}
.encabezado .navigator ul li a:hover {
  /* color: map-get($color, "secundary" ); */
  text-decoration: underline 2px;
}
.encabezado .navigator ul button {
  height: 90px;
  border-radius: 4px;
  padding: 10px 11px;
  background-color: #0098C0;
  color: #FFFFFF;
  border: #0098C0 3px solid;
  cursor: pointer;
  font-weight: bold;
  transition: all 200ms ease-in-out;
}
.encabezado .navigator ul button:hover {
  border-radius: 4px;
  padding: 10px 11px;
  background-color: #0098C0;
  color: #FFFFFF;
  border: #0098C0 3px solid;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 5px;
  box-shadow: 0px 12px 14px -5px #0098C0;
  -moz-box-shadow: 0px 12px 14px -5px #0098C0;
  box-shadow: 0px 12px 14px -5px #0098C0;
}
.encabezado .navigator ul button a {
  display: flex;
  align-items: center;
}
.encabezado .navigator ul button a p {
  font-size: 14px;
}
.encabezado .navigator ul button a img {
  width: 20px;
  margin-left: 8px;
}

/** Media Queries **/
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/1.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.barra {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  background-color: rgb(10, 10, 10);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 2rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-servicios .servicio.disabled {
  opacity: 0.5; /* Reduce la opacidad para indicar que está deshabilitado */
  cursor: not-allowed; /* Cambia el cursor a "no permitido" */
  pointer-events: none; /* Deshabilita eventos de puntero para evitar interacciones */
}
.listado-servicios .servicio.seleccionado {
  background-color: rgb(92, 43, 240);
}
.listado-servicios .servicio.seleccionado p {
  color: rgb(219, 126, 228);
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio p {
  color: rgb(48, 75, 82);
  margin: 0 0 1.25rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: rgb(52, 119, 52);
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}
.listado-servicios .servicio .green {
  background-color: #3fd13f;
}
.listado-servicios .servicio .red {
  background-color: #f96937;
}
.listado-servicios .servicio .yellow {
  background-color: #c5cb46;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid #e1e1e1;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.uno {
  padding-left: 15rem;
  padding-right: 15rem;
  padding-top: 5rem;
}

.form {
  font-size: 2rem;
  margin: 3rem;
}

.bus {
  font-size: 2rem;
}

.citas, .servicios {
  list-style: none;
  display: flex;
  padding: 2rem 15rem;
  justify-content: space-evenly;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #0da6f3;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.citas li .product, .servicios li .product {
  color: #cb0000;
}
.citas li .total, .servicios li .total {
  color: #329f00;
}

.servicios .acciones {
  margin: 0;
}
.servicios .boton,
.servicios .boton-eliminar {
  margin: 0;
}

/*footer*/
footer {
  background-color: rgb(70, 140, 145);
  display: flex;
  flex-direction: column;
  margin-top: 15rem;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  list-style-type: none;
  text-decoration: none;
}

.contenedor-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 1rem 2rem;
}

.titulos-footer {
  font-weight: 250;
  display: inline-block;
  padding-bottom: 0.6rem;
  margin: 1rem 0;
  position: relative;
  color: rgb(255, 255, 255);
}

.titulos-footer:before {
  content: "";
  position: absolute;
  width: 70%;
  height: 1px;
  bottom: 0;
  left: 0%;
  border-bottom: 0.2rem solid #30a894;
}

.sobre-mi p {
  font-size: 2rem;
  line-height: 2rem;
  padding-bottom: 1rem;
  color: #e0e0e0;
}

.redes-sociales {
  display: flex;
  gap: 1rem;
  margin: 0.5rem 0;
  list-style-type: none;
}

.redes-sociales a {
  padding: 2.6rem 1.2rem 1rem 1rem;
  background-color: #303030;
  border-radius: 1.2rem;
}

.redes-sociales a:hover {
  background-color: #39ad77;
}

.fa-brands {
  font-size: 4rem;
}

.navegacion li a {
  display: inline-block;
  margin-bottom: 0.8rem;
  color: #e0e0e0;
}

.navegacion li a:hover {
  color: white;
}

.informacion-contacto li {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.informacion-contacto p {
  color: #e0e0e0;
}

.copy {
  text-align: center;
  font-size: 0.9rem;
  line-height: 1.4rem;
  background-color: #ffffff;
  background-color: #303030;
  padding: 0.5rem;
}

@media (min-width: 700px) {
  .contenedor-footer {
    flex-direction: row;
  }
  .sobre-mi {
    width: 40%;
  }
}
.contenedor-inicio {
  padding: 0px 90px;
}
.contenedor-inicio h1 {
  color: #b71540;
  margin-top: 32px;
}
.contenedor-inicio h3 {
  color: #fad390;
}
.contenedor-inicio .panel-cajas {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: space-between;
}
.contenedor-inicio .panel-cajas .caja {
  text-align: center;
  width: 32%;
  margin-top: 24px;
  padding: 19px 13px;
  border: 1px darkgray solid;
  box-shadow: 8px 10px 18px 3px rgb(184, 184, 184);
  -moz-box-shadow: 8px 10px 18px 3px rgb(184, 184, 184);
  box-shadow: 8px 10px 18px 3px rgb(184, 184, 184);
}
.contenedor-inicio .panel-cajas .caja .title p {
  font-weight: bold;
}
.contenedor-inicio .panel-cajas .caja .boton button {
  border-radius: 4px;
  padding: 10px 95px;
  background-color: #0098C0;
  color: #FFFFFF;
  border: #0098C0 3px solid;
  cursor: pointer;
  font-weight: bold;
  transition: all 200ms ease-in-out;
}
.contenedor-inicio .panel-cajas .caja .boton button a {
  color: white;
}
.contenedor-inicio .panel-cajas .caja .boton button:hover {
  border-radius: 4px;
  padding: 10px 95px;
  background-color: #0098C0;
  color: #FFFFFF;
  border: #0098C0 3px solid;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 0px;
  box-shadow: 0px 12px 14px -5px #0098C0;
  -moz-box-shadow: 0px 12px 14px -5px #0098C0;
  box-shadow: 0px 12px 14px -5px #0098C0;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: grid;
  margin-bottom: 2rem;
  align-items: center;
  font-size: 2rem;
  padding: 5rem;
  margin: 5rem;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 0.1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: #575757;
  color: #FFFFFF;
  cursor: not-allowed;
}

.propiedades {
  margin-left: 22rem;
  margin-top: 5rem;
  font-size: 2rem;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #0a85c3;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

.paginacion {
  padding: 5rem 0;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #0a3d62;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}