@use 'variables1' as v;
@use 'mixins1' as m;

.acciones {
    margin-top: v.$separacion;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    a {
        
        color: v.$blanco;
        display: block;
        margin-bottom: calc(v.$separacion / 2);
        text-align: center;
    }
}